@import url(https://fonts.googleapis.com/css?family=Open+Sans:600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:600&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.Nav {
    font-family: "Open Sans", sans-serif;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 20px 20px 5px 20px;
    border-bottom: 1px solid #d5e0f2;
}

.home {
    margin-right: auto;
    font-size: 1.5em;
    color: #112e2d;
}

.home:hover {
    color: red;
    text-decoration: none;
    border-bottom: 2px solid red;
}

.nav-list {
    list-style: none;
    /* background-color: #c0d1c5; */
}

.nav-list li {
    display: inline-block;
    padding: 0px 20px;
}

.nav-list li a {
    color: #112e2d;
    text-decoration: none;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
}

.nav-list li a:hover {
    color: red;
    /* padding: 20px 30px; */
    border-bottom: 3px solid red;
}
.home-container {
    text-align: center;
}

h1 {
    padding-top: 20px;
    padding-bottom: 20px;
    font-family: "Open Sans", sans-serif;
}

.profile-pic img{
    max-width: 20%;
    max-height: 30%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    /* border: 1px solid black; */
}

.about-container {
    text-align: center;
    margin-top: 30px;
}

.das-popup {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
  }
  
.das-popup .das-popup-text {
    visibility: hidden;
    width: 300px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -60px;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
  }
  
  .das-popup .das-popup-text::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
  }
  
  .das-popup:hover .das-popup-text {
    visibility: visible;
    opacity: 1;
  }
